footer {
  /* position: absolute;
  bottom: -55px; */
  width: 100%;
  background-color: #333;
  /* background-color: #11063f; */
  color:#fff;
}

#unorderedlist a:hover{
  color: rgb(216, 26, 26) !important;
}

#keylinks a:hover{
  color: orange !important;
}

#followusfacebook{
  animation: loader 5s infinite;
}

@keyframes loader {
  50% {transform: rotate(180deg);}
}

.slideRight {
  position: relative;
  animation-name: rightslider;
  animation-duration: 5s;
}

@keyframes rightslider {
  0%   { left: -200px; top:0px;}
  25%  { left:0px; top:0px;}
}

.slideLeft {
  position: relative;
  animation-name: leftslider;
  animation-duration: 5s;
}

@keyframes leftslider {
  0%   { left: 200px; top:0px;}
  25%  { left: 0px; top:0px;}
}

